import uniCard from "@/components/uni-card/uni-card.vue"
import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	components: {uniCard,uniPopup},
	data() {
		return {
			tabBar: [{
					"title": "请求"
				},
				{
					"title": "回复"
				},
			],
			swiperHeight:'',
			currentTab: 0,
			type:'',
		}
	},
	onBackPress() {
		if (this.type !== '') {
			this.type = '';
			return true;
		}
	},
	methods: {
		onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
			// 获取屏幕高度
			let windowHeight=uni.getSystemInfoSync().windowHeight;
			// 设置纵向滑动容器高度
			this.swiperHeight=windowHeight-42+'px';
		},
		clickTab(e) {
			this.currentTab = e;
		},
		changeTab(e) {
			this.currentTab = e.mp.detail.current;
		},
		// 驳回
		dont_agree(e){
			this.date = e.target.value;
			uni.showToast({
				title: "已发至房东"
			})
		},
		agree(time){
			let that=this;
			uni.showModal({
				content: `确定更改退租日期为${time}`,
				success: function (res) {
					if (res.confirm) {
						that.type = 'bottom-share';
					} else if (res.cancel) {
						
					}
				}
			});
		},
		// 模态框弹出
		hidePopup(type) {
			this.type = type;
		},
		// 跳转首页
		gohome(){
			uni.navigateTo({
				url: `../../index/index`,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
